#Career {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 40px;
  min-height: 100vh;
}

#CareerOne {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(169, 196, 46, 0.299);
  width: 500px;
  height: 60vh;
  margin: 10px;
  padding: 20px;
  border-radius: 30px;
  text-align: center;
  font-size: 1.2em;
}

