@import url("https://fonts.googleapis.com/css2?family=Song+Myung&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap");

body {
font-family: "Nanum Gothic Script";
line-height: 1.5;
padding-left: 10px;
font-style: normal;
background: rgba(136, 157, 44, 0.299);
margin: 0px 0px 0px 200px;
}

header {
 text-align: center;
 font-size: 60px;
 background-color: rgba(136, 157, 44, 0.299);
}

.container {
 display: grid;
 grid-template-columns: 450px 1fr;
}

nav {
 font-family: "Nanum Gothic Script";
 margin-left: 100px;
 font-size: 40px;
}

main{
font-family: "Nanum Gothic Script";
margin-left: 300px;
background-color: lightgoldenrodyellow;
font-size: 70px;
}

footer {
  font-family: "Nanum Gothic Script";
  line-height: 1.5;
  font-size: 1.2em;
  font-style: normal;
  font-family: "Nanum Gothic Script";
  text-align: center;
  margin-top: 20px;
  color: white;
}

footer hr {
  height: 1px;
  background-color: white;
  border: 0px;
}

/* a:hover, a:active { 
 background-color: #f2ff00; 
} */

button {
    font-family: "Nanum Gothic Script";
    background-color: rgba(115, 132, 38, 0.299);
    color: white;
    padding: 14px 20px;
    margin: 5px;
    font-size: 1.2em;
    cursor: pointer;
    display: inline-block;
    border: none;
    border-radius: 5px;
  }

  input {
    width:200px;
    height: 80px;
    font-size:60px;
  }
  
/* Home */
.Home {
  display: grid;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0px;
}
.Home .top {
  display: flex;
  justify-content: center;
  align-items: center;
}
.Home .top .Me_img {
  padding-bottom: 20px;
  padding-right: 200px;
}

.Home .top .Me_content {
  color: white;
  font-size: 35px;
  line-height: 100%;
}

.Home .top .Me_content hr {
  height: 5px;
  background-color: white;
  border: 0px;
}
.Home .top .Me_content h5 {
  margin-top: 10px;
}
