#Ability {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 40px;
  min-height: 100vh;
  flex-wrap: wrap;
}

.AbilityBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 300px;
  height: 30vh;
  margin: 10px;
  padding: 20px;
  border-radius: 30px;
  text-align: center;
  background-color: rgba(130, 144, 67, 0.299);
}

#AbilityOne {
  background-color: rgba(182, 212, 51, 0.299);
}
  
#AbilityTwo {
  background-color: rgba(161, 186, 45, 0.299);
}

#AbilityThree {
  background-color: rgba(123, 137, 59, 0.299);
}

#AbilityFour {
  background-color: rgba(39, 46, 14, 0.299);
}