@import url("https://fonts.googleapis.com/css2?family=Song+Myung&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap");

.sidebar {
    font-family: "Nanum Gothic Script";
    width: 200px;
    height: 1000px;
    background-color: rgba(136, 157, 44, 0.299);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    position: fixed;
    left: 0;
    top: 0;
  }
  
  .sidebar a {
    text-decoration: none;

  }
  
  .sidebar-item {
    width: 100%;
    padding: 15px;
    text-align: center;
    color: white; /* 흰색 글씨색 */
    cursor: pointer;
    transition: background-color 0.3s;
    font-size: 1.2em;
    border-radius: 30px;
  }
  
  .sidebar-item:hover {
    background-color: #fff; /* 마우스 호버 시 배경색 변경 */
    width: 100px;
    border-radius: 30px;
  }
  
  .sidebar-item.active {
    background-color: #fff; /* 활성화된 메뉴의 배경색 */
    color: rgba(136, 157, 44, 0.299); /* 활성화된 메뉴의 글씨색 */
    width: 100px;
    border-radius: 30px;
  }
  