@import url("https://fonts.googleapis.com/css2?family=Song+Myung&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap");
#login {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

#loginBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(169, 196, 46, 0.299);
  width: 600px;
  height: 50vh;
  margin: 10px;
  padding: 20px;
  border-radius: 30px;
  text-align: center;
}

input {
  position: relative;
  overflow: hidden;
  width: 600px;
  height: 60px;
  border: solid 1px #dadada;
  background: #fff;
  box-sizing: border-box;
  font-size: 20px;
  padding: 10px;
}

#btn {
  font-family: 'Nanum Gothic Script';
  font-weight: bolder;
  font-size: 20px;
  display: block;
  width: 100%;
  height: 60px;
  margin: 20px 0px 0px 0px;
  cursor: pointer;
  text-align: center;
  color: #000;
  border: none;
  border-radius: 0;
  background-color: rgba(136, 157, 44, 0.299);
}

label {
  font-family: 'Nanum Gothic Script';
  font-weight: bolder;
  font-size: 44px;
  margin-bottom: 20px;
}
