#resume {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 40px;
  min-height: 100vh;
}

#resumeOne {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(169, 196, 46, 0.299);
  width: 400px;
  height: 60vh;
  margin: 10px;
  border-radius: 30px;
  text-align: center;
  font-size: 1.2em;
}

#resumeOne img {
  width: 160px;
  height: auto;
  border-radius: 10px;
}

#resumeOne div {
  margin: 10px;
}

