#strength a:link,
#strength a:visited {
  text-decoration: none;
  display: inline-block;
  width: auto;
  font-size: 1em;
  margin: 5px;
  color: black;
  background-color: none;
}

#strength a:hover,
#strength a:active {
  background-color: lightgoldenrodyellow;
}

#strength legend {
  font-size: 1.2em;
}